$(document).ready(function (){
    var $rentForm  = $('#rent-form');
    $rentForm.submit(function(){
        var response;

        $.ajax({
            type: "POST",
            url: "profile/rent",
            data: $(this).serialize(),
            success: function(result) {
                if (result == true) {
                    response = '<div class="alert alert-success">'+ $success +'</div>';
                }
                else {
                    response = '<div class="alert alert-warning">'+ $error +'</div>';
                }
            }
         });
    });
});